:root {
  --color-light-primary: #FFFFFF;
  --color-light-secondary: #F3F3F3;
  --color-dark-primary: #1A1A1A;
  --color-dark-secondary: #232323;
  --color-gray-primary: #CCCCCC;
  --color-gray-secondary: #F4F5F7;
  --color-blue-primary: #ffb400;
  --color-blue-secondary: #FFE767;
  --color-blue-third: #CB8000;
  --transition-duration-primary: 0.5s;
  --transition-duration-secondary: 0.5s;
  --transition-function-primary: ease-in-out;
  --transition-function-secondary: cubic-bezier(.4,0,.2,1);
  --border-radius-primary: 6px;
  --border-radius-secondary: 15px;
  --main-font: OpenSans, Helvetica, sans-serif;
  --second-font: PermianSans, Arial, sans-serif;
}

/* Large desktops */

body,
html {
  width: 100%;
  height: 100%;
}

main {
  position: relative;
  margin-bottom: 82px;
  padding-top: 88px;
}

html > div[style] {
  display: none !important;
}

ul,
menu,
ol,
section,
figure,
aside {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.wrapper {
  display: block;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
}

.page-breadcrumbs {
  margin-bottom: 24px;
  padding: 0;
  font-size: 14px;
}

.page-breadcrumbs a,
.page-breadcrumbs strong {
  margin: 0 10px;
}

.page-breadcrumbs > span > span > a {
  margin-left: 0;
}

.page-breadcrumbs strong {
  margin-right: 0;
}

.stop-scrolling {
  position: fixed;
  width: 100%;
  height: 100%;
  max-height: 100%;
  overflow-y: scroll;
}

body,
html {
  color: var(--color-dark-primary);
  font-size: 16px;
  font-weight: normal;
  font-family: var(--main-font);
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
}

h1 {
  margin: 72px 0 26px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 24px;
  line-height: 32px;
}

h2 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 20px;
  line-height: 30px;
}

h3 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 18px;
  line-height: 24px;
}

h4 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 24px;
}

h5 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 18px;
  line-height: 24px;
}

h6 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--main-font);
  font-size: 16px;
  line-height: 20px;
}

.hero__headline {
  margin: 72px 0 26px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 24px;
  line-height: 32px;
}

strong {
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 20px;
}

small {
  margin: 16px 0;
  padding: 0;
  font-size: 13px;
  line-height: 22px;
}

p {
  margin: 14px 0;
  padding: 0;
}

p:last-child {
  margin-bottom: 0;
}

a {
  margin: 0;
  padding: 0;
}

a,
a:active,
a:visited {
  position: relative;
  color: var(--color-blue-primary);
  text-decoration: none;
  transition: color var(--transition-duration-primary) var(--transition-function-primary);
}

a.link_underline,
a:active.link_underline,
a:visited.link_underline {
  padding-bottom: 2px;
  color: var(--color-blue-primary) !important;
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 20px;
  text-overflow: ellipsis;
  text-shadow: none;
  background: linear-gradient(90deg, var(--color-blue-primary), var(--color-blue-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  box-decoration-break: clone;
}

a.link_underline:after,
a:active.link_underline:after,
a:visited.link_underline:after {
  position: absolute;
  bottom: 1px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--color-blue-primary), var(--color-blue-secondary));
  transition: width var(--transition-duration-primary) var(--transition-function-secondary);
  content: "";
}

a:hover {
  color: var(--color-blue-secondary);
}

a:hover.link_underline:after {
  width: 100%;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
  margin-top: 0;
  margin-left: 0;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

.search-block__form {
  position: absolute;
  top: 50%;
  right: 0;
  display: none;
  flex-direction: row;
  align-items: center;
  width: 0;
  height: 40px;
  margin-left: auto;
  transform: translate(0, -50%);
  transition: width var(--transition-duration-secondary) var(--transition-function-secondary);
}

.search-block__form.is-active {
  width: 100%;
}

.search-block__form.is-active .search-block__input {
  padding: 0 52px;
  border: 1px solid var(--color-gray-primary);
}

.search-block__form.is-active .search-block__button {
  left: 8px;
}

.search-block__form.is-active .search-block__close {
  display: block;
}

.search-block__input {
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 10;
  width: 100%;
  max-width: 100%;
  min-height: 30px;
  padding: 0 8px;
  border: 1px solid transparent;
  border-radius: 3px;
  transform: translate(0, -50%);
  transition: width var(--transition-duration-primary) var(--transition-function-primary);
}

.search-block__button {
  position: absolute;
  top: 6px;
  left: -34px;
  z-index: 11;
  display: block;
  width: 30px;
  height: 30px;
  font-size: 20px;
  cursor: pointer;
  transition: color var(--transition-duration-primary) var(--transition-function-primary);
}

.search-block__button path {
  transition: stroke var(--transition-duration-primary) var(--transition-function-primary);
}

.search-block__button:hover {
  color: var(--color-blue-secondary);
}

.search-block__button:hover path {
  stroke: var(--color-blue-secondary);
}

.search-block__close {
  position: absolute;
  top: 50%;
  right: 10px;
  z-index: 10;
  display: none;
  width: 13px;
  height: 13px;
  transform: translateY(-50%);
  cursor: pointer;
}

.search-block__close svg {
  position: absolute;
}

.search-block__close:hover path {
  stroke: var(--color-blue-secondary);
}

.top-bar {
  background: var(--color-light-primary);
}

.top-bar__inner {
  position: relative;
  display: flex;
  align-items: center;
  height: 40px;
}

.top-bar__item {
  position: relative;
  margin-right: 32px;
  color: var(--color-blue-primary);
}

.top-bar__item-icon {
  margin-right: 3px;
}

.top-bar__item-icon_logo {
  width: 110px;
  padding-top: 3px;
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
}

.top-bar__item-icon_logo path {
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
}

.top-bar__item-icon_logo path:first-child {
  fill: var(--color-blue-primary);
}

.top-bar__item-icon_logo path:not(:first-child) {
  stroke: var(--color-blue-primary);
}

.top-bar__item-icon_logo path:first-child {
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
}

.top-bar__item:hover .top-bar__item-icon_logo path:first-child {
  fill: var(--color-blue-secondary);
}

.top-bar__item:hover .top-bar__item-icon_logo path:not(:first-child) {
  stroke: var(--color-blue-secondary);
}

.top-bar__item_parent {
  display: flex;
  align-items: center;
  height: 40px;
}

.top-bar__link {
  display: flex;
  align-items: center;
  padding: 8px 0;
}

.top-bar__dropdown {
  position: absolute;
  top: 80px;
  left: 0;
  z-index: 10;
  display: none;
  width: 100%;
  min-width: 240px;
  max-height: 500px;
  padding: 8px 16px;
  background: var(--color-light-primary);
  border-radius: var(--border-radius-primary);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.16);
  opacity: 0;
  transition: max-height var(--transition-duration-secondary) var(--transition-function-primary);
}

.top-bar__dropdown::before,
.top-bar__dropdown::after {
  position: absolute;
  top: -20px;
  left: 28px;
  border: 10px solid transparent;
  border-bottom: 10px solid rgba(0, 0, 0, 0.2);
  content: "";
}

.top-bar__dropdown::after {
  top: -20px;
  border-bottom: 10px solid var(--color-light-primary);
}

.top-bar__copy-btn {
  cursor: pointer;
}

.top-bar__copy-text {
  width: 272px;
  height: 22px;
  color: var(--color-blue-primary);
  border: none;
  cursor: pointer;
}

.top-bar__copy-text:hover {
  color: var(--color-blue-secondary);
}

.top-bar__copy-icon {
  margin-right: 3px;
}

.top-bar__success {
  position: relative;
}

.top-bar__success::before,
.top-bar__success::after {
  position: absolute;
  top: 0;
  left: 100%;
  height: 3px;
  background: var(--color-blue-primary);
  opacity: 0;
  transition: opacity var(--transition-duration-primary) var(--transition-function-primary);
  content: "";
}

.top-bar__success::before {
  width: 8px;
  transform: translate(10px, 13px) rotate(45deg);
}

.top-bar__success::after {
  width: 14px;
  transform: translate(14px, 12px) rotate(-45deg);
}

.top-bar__success.top-bar__success_active::before,
.top-bar__success.top-bar__success_active::after {
  opacity: 1;
}

.top-bar .search-block {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}

.header {
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 9;
  width: 100%;
  min-height: 88px;
  padding: 0;
  background: var(--color-blue-primary);
  transition: transform var(--transition-duration-primary) var(--transition-function-primary);
}

.header > div {
  background: var(--color-blue-primary);
}

.header_fixed {
  position: fixed;
  top: 0;
  transform: translateY(0);
}

.header_hidden {
  position: fixed;
  transform: translateY(-199px);
}

.header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__logo {
  width: 340px;
  margin-right: auto;
}

.header__logo .logo__link {
  display: flex;
}

.header__logo .logo__img {
  max-width: 150px;
}

.header__logo .logo__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: 14px;
  color: var(--color-light-primary);
  text-align: left;
}

.header__logo .logo__text_strong {
  margin-bottom: 4px;
  font-size: 20px;
  line-height: 20px;
}

.header__logo .logo__text_hidden-tablet {
  display: block;
  font-size: 12px;
  line-height: 16px;
}

.header__button-burger-outer {
  position: absolute;
  top: 24px;
  right: 60px;
  display: none;
}

.header__nav-bar {
  position: relative;
  background: var(--color-blue-primary);
}

.header__nav-bar.header__nav-bar_active {
  position: fixed;
  transform: translateY(0%);
}

.header__nav-bar .menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__nav-bar .menu__item {
  position: relative;
  display: flex;
  align-items: center;
  height: 88px;
  margin-right: 10px;
}

.header__nav-bar .menu__item_active .menu__link {
  box-shadow: inset 0 0 0 1px var(--color-light-primary);
}

.header__nav-bar .menu__item:last-child {
  margin-right: 0;
  margin-bottom: 0 !important;
}

.header__nav-bar .menu__link {
  position: relative;
  padding: 10px;
  color: var(--color-light-primary);
  line-height: 15px;
  text-align: center;
  border-radius: var(--border-radius-primary);
  cursor: pointer;
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
}

.header__nav-bar .menu__link:hover {
  box-shadow: inset 0 0 0 1px #fff;
}

.header__nav-bar .menu__children {
  position: absolute;
  top: 92px;
  left: -24px;
  z-index: 4;
  display: none;
  min-width: 250px;
  max-height: 600px;
  padding: 16px 24px;
  background: var(--color-light-primary);
  border-radius: var(--border-radius-primary);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.16);
  opacity: 0;
  transition: max-height var(--transition-duration-secondary) var(--transition-function-primary);
}

.header__nav-bar .menu__children::before,
.header__nav-bar .menu__children::after {
  position: absolute;
  top: -20px;
  left: 40px;
  border: 10px solid transparent;
  border-bottom: 10px solid rgba(0, 0, 0, 0.2);
  content: "";
}

.header__nav-bar .menu__children::after {
  top: -20px;
  border-bottom: 10px solid var(--color-light-primary);
}

.header__nav-bar .menu__children.menu__children_active {
  max-height: 500px;
  padding: 0 8px 8px 8px;
  transition: max-height var(--transition-duration-primary) var(--transition-function-primary);
}

.header__nav-bar .menu__children .menu__item {
  display: block;
  height: auto;
  margin-right: 0;
  margin-bottom: 12px;
  padding: 0;
}

.header__nav-bar .menu__children .menu__item:hover .menu__link {
  color: var(--color-blue-secondary);
}

.header__nav-bar .menu__children .menu__link {
  display: block;
  padding: 0;
  color: var(--color-dark-primary);
  font-size: 16px;
  line-height: 24px;
  text-align: left;
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
}

.main-page-about {
  position: relative;
  margin-bottom: 96px;
  overflow: hidden;
}

.main-page-about .background {
  position: absolute;
  top: -128px;
  right: 0;
  left: 0;
  z-index: -3;
}

.main-page-about .background:before,
.main-page-about .background:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.3;
  content: "";
}

.main-page-about .background:before {
  z-index: -2;
  background: var(--color-blue-primary);
}

.main-page-about .background:after {
  z-index: -1;
  background: url(wp-content/themes/supertheme/assets/images/general/dotted-overlay.png);
}

.main-page-about .background__relation {
  position: relative;
  z-index: -3;
}

.main-page-about .background__ratio {
  height: 0;
  padding-top: 56.25%;
  /* 100 / 16 * 9 */
}

.main-page-about .background__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.main-page-about .article {
  position: relative;
  display: block;
  padding: 11% 0;
  color: var(--color-light-primary);
}

.main-page-about .article__headline {
  margin-top: 50px;
  color: inherit;
}

.main-page-about .article__type {
  padding-right: 5px;
}

.main-page-about .article__type-inner {
  position: absolute;
  left: 0;
  margin: 0;
  color: inherit;
  text-align: left;
}

.main-page-about .article__link-button {
  position: relative;
  margin-top: 72px;
}

.main-page-about .typed-cursor {
  color: var(--color-light-primary);
  opacity: 1;
  animation: blink 0.7s infinite;
}

